@charset "UTF-8";
header {
  position: relative;
  border-bottom: 1px solid #808080;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  background: #0097ff;
  z-index: 999;
}
header h1 {
  font-size: 1.8em;
}
header h1 a {
  color: #ffffff;
}
header .headerYMD {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  gap: 30px;
}
header .headerYMD input {
  background: #0097ff;
  color: #fff;
}
header .headerYMD input[type=date] {
  /* Chrome 및 Safari (WebKit 엔진 기반) */
  /* Firefox */
  /* Edge 및 Internet Explorer */
}
header .headerYMD input[type=date]::-webkit-calendar-picker-indicator {
  filter: invert(1);
}
header .headerYMD input[type=date]::-moz-calendar-picker-indicator {
  filter: invert(1);
}
header .headerYMD input[type=date]::-ms-clear {
  filter: invert(1);
}
header .headerYMD a {
  color: #fff;
}
header .headerYMD button {
  padding: 5px;
  border-radius: 5px;
  background: #00dfa2;
  color: #000000;
  cursor: pointer;
}
header .headerYMD button:hover {
  background: #149f79;
  color: #ffffff;
}

@media screen and (max-width: 750px) {
  header {
    padding: 0 5px;
  }
  header .logo {
    width: 70px;
    height: 100%;
    overflow: hidden;
  }
  header .logo a {
    width: 100%;
    height: 100%;
    display: block;
    background: url(/public/imgs/calendar_logo.png) no-repeat;
    text-indent: -999999999px;
    animation: Logoanimate 1s ease-in-out;
    animation-iteration-count: 1;
    transition: 0.5s;
  }
  header .headerYMD {
    display: flex;
    justify-content: center;
    gap: 10px;
  }
  @keyframes Logoanimate {
    0% {
      transform: translateY(100%);
    }
    100% {
      transform: translateY(0%);
    }
  }
}

